<template>
  <div>
    <h1>英雄列表</h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="英雄id" width="230"> </el-table-column>

      <el-table-column prop="name" label="英雄名称"> </el-table-column>
      <el-table-column prop="avater" label="头像">
        <template slot-scope="scope">
          <img :src="scope.row.avater" style="height:3rem;" alt="" />
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="180">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="$router.push(`/heros/edit/${scope.row._id}`)">编辑</el-button>
          <el-button type="text" size="small" @click="remove(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: []
    }
  },
  created() {
    this.fetch()
  },

  methods: {
    /**
     * 获取所有物品
     */
    async fetch() {
      const res = await this.$http.get('rest/heros')
      this.items = res.data
    },
    /**
     * 删除物品
     */
    async remove(row) {
      this.$confirm(`是否确定要删除英雄 ${row.name}`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await this.$http.delete(`rest/heros/${row._id}`)
        this.$message({
          type: 'success',
          message: '删除成功'
        })
        this.fetch()
      })
    }
  }
}
</script>

